body {
  font: normal 16px/150% Arial, Helvetica, sans-serif;
}

login {
  padding-top: 60px;
  display: block;
  text-align: center;
  height: 100%;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}

.form-signin .form-control:focus {
  z-index: 2;
}

#inputCi {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

#inputPassword {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.field-icon {
  float: right;
  position: relative;
  z-index: 2;
  left: -12px;
  top: -37px;
  cursor: pointer;
}

sellers-track .map {
  border: 1px dotted #20262e;
}

logs-report .container-fluid {
  padding-top: 1.5%;
  padding-left: 7%;
  padding-right: 12%;
  position: relative;
}

logs-report .container-fluid .row {
  background-color: #dcddde;
  border: 1px solid #ccc !important;
  height: 400px;
  overflow-y: scroll;
}

logs-report .container-fluid .logs {
  width: 100%;
}

